<template>
  <v-container id="train" fluid tag="section">
    <base-material-card>
      <v-data-table :headers="headers" :items="trains" :search="search"></v-data-table>
    </base-material-card>
  </v-container>
</template>

<script>
  export default {
    name: '',

    data() {
      return {
        trains: [],
        search: '',
        headers: [{
            text: '试卷名称',
            align: 'start',
            filterable: false,
            value: 'name',
          },
          {
            text: '分数',
            value: 'grade'
          },
          {
            text: '时长',
            value: 'train_time'
          },
          {
            text: '日期',
            value: 'time'
          },
        ],

      }
    },
    created: function () {
      this.get_trains();
    },

    methods: {
      get_trains: function () {
        var that = this;
        this.$axios({
          method: "get",
          url: "/api/get_trains/",
          params: {
            stu_id: that.$global.userMsg.id,
          }
        }).then(function (response) {
          if (response.data.error_num == 0) {
            console.log("历史记录", response.data);
            that.trains = response.data.trains.reverse();
            for (var i = 0; i < response.data.trains.length; i++) {
              that.trains[i].time = that.trains[i].time.slice(0, 10)
            }
            // alert("获取成功！");
          } else {
            alert("获取失败！");
          }
        })
      },
    },
  }
</script>